import {Id} from '../../../types'
import {generateTree, TreeNode} from './utils/tree'
import {
  HORIZONTAL_SPACE,
  VERTICAL_SPACE,
  DEFAULT_WIDTH,
  DEFAULT_HEIGHT,
  ANIMATION_DURATION,
} from './utils/conf'
import {getAutoSizeValue} from './utils/auto-size'
import type {
  NodeData,
  LineData,
  MarkData,
  NoteData,
} from '@ej/erg-renderer/src/erg/types/graph'

import TWEEN from '@tweenjs/tween.js'

export const hierarchicalLayout = async (
  data: {
    nodeDataArray: NodeData[]
    linkDataArray: LineData[]
    markDataArray?: MarkData[]
    noteDataArray?: NoteData[]
  },
  updateFunc?: () => void,
) => {
  const {nodeDataArray, linkDataArray, markDataArray, noteDataArray} = data

  // 从多到少排序的图形分组
  const layers = createLayers(nodeDataArray, linkDataArray).sort(
    (a, b) => b.children.length - a.children.length,
  )

  // 布局实体（x, y, w, h）
  layoutNodes(nodeDataArray, layers).then((maxX) => {
    // 布局备注框
    const maxY = layoutMarks(markDataArray, maxX, -DEFAULT_HEIGHT)

    // 布局备注文本
    layoutNotes(noteDataArray, maxX, maxY)
  })

  function animate() {
    const tweens = TWEEN.getAll()
    if (!tweens.length) return
    requestAnimationFrame(animate)
    TWEEN.update()
    updateFunc?.()
  }
  animate()
}

/**
 * 生成layer矩阵
 */
const createLayers = (nodes: NodeData[], lines: LineData[]) => {
  /**
   * 计算每个节点的深度和父节点
   */
  const nodeParents = new Map<Id, Set<Id>>()

  for (const node of nodes) {
    nodeParents.set(node.id, new Set())
  }

  for (const line of lines) {
    const parents = nodeParents.get(line.to)

    // 关系中包含不存在的实体不计入
    const existToNode = !!parents
    const existFromNode = !!nodeParents.get(line.from)
    if (!existFromNode || !existToNode) continue

    parents.add(line.from)
  }

  /**
   * 生成layer矩阵
   */
  const layers: TreeNode[] = []

  let allDone = false
  while (!allDone) {
    if (!nodeParents.size) {
      allDone = false
      break
    }

    let minParentsId = null
    // 每循环一次为一组，对应第一层[]
    for (const [toId, fromIds] of nodeParents) {
      // 找到父级最少的节点作为顶级节点
      const ifEmpty = minParentsId === null
      const ifLess = fromIds.size < nodeParents.get(minParentsId)?.size
      const ifMin = minParentsId === 0
      if (ifEmpty || ifLess) minParentsId = toId
      if (ifMin) break
    }

    nodeParents.delete(minParentsId)

    const groupData = generateTree(nodeParents, minParentsId)
    layers.push(groupData)
  }

  return layers
}

/**
 * 根据矩阵，重新设定各个实体的x,y,w,h
 */
const layoutNodes = async (nodes: NodeData[], layers: TreeNode[]) => {
  const nodeMap = new Map<Id, NodeData>()
  nodes.map((node) => nodeMap.set(node.id, node))

  await Promise.all(autoSize(nodes))

  return Promise.resolve(
    layers.reduce(
      (x, group) => (x = recursionGroup(nodeMap, [group], x, VERTICAL_SPACE)),
      HORIZONTAL_SPACE,
    ),
  )
}

/**
 * 自适应实体大小: w, h
 */
const autoSize = (nodes: NodeData[]) => {
  return nodes.map((node) => {
    if (['inclusive', 'exclusive'].includes(node.type)) return
    const autoSizeValue = getAutoSizeValue(node)

    return new Promise<void>((resolve) => {
      new TWEEN.Tween(node)
        .to(
          {width: autoSizeValue.width, height: autoSizeValue.height},
          ANIMATION_DURATION / 2,
        )
        .easing(TWEEN.Easing.Quadratic.InOut)
        .start()
        .onComplete(() => resolve())
      // node.width = autoSizeValue.width
      // node.height = autoSizeValue.height
    })
  })
}

/**
 * 布局实体: x, y
 */
const recursionGroup = (nodeMap, rows, x, y) => {
  const curRowMaxHeight = Math.max(
    ...rows.map((it) => nodeMap.get(it.id).height || DEFAULT_HEIGHT),
  )
  const nextBeginY = y + curRowMaxHeight + VERTICAL_SPACE
  let maxX = x
  for (let i = 0; i < rows.length; i++) {
    const row = nodeMap.get(rows[i].id)
    new TWEEN.Tween(row)
      .to({x: maxX, y: y}, ANIMATION_DURATION)
      .easing(TWEEN.Easing.Quadratic.InOut)
      .start()
    const curX = maxX
    // row.x = maxX
    // row.y = y
    maxX += row.width + HORIZONTAL_SPACE
    if (rows[i].children?.length)
      maxX = Math.max(
        recursionGroup(nodeMap, rows[i].children, curX, nextBeginY),
        maxX,
      )
  }
  return maxX
}

/**
 * 布局备注框
 */
const layoutMarks = (markDataArray, startX, startY) => {
  let y = startY
  markDataArray?.map((it) => {
    y += VERTICAL_SPACE + DEFAULT_HEIGHT

    new TWEEN.Tween(it)
      .to(
        {x: startX, y, width: DEFAULT_WIDTH, height: DEFAULT_HEIGHT},
        ANIMATION_DURATION,
      )
      .easing(TWEEN.Easing.Quadratic.InOut)
      .start()
    // it.x = maxX
    // it.y = y += VERTICAL_SPACE + DEFAULT_HEIGHT
    // it.width = DEFAULT_WIDTH
    // it.height = DEFAULT_HEIGHT
  })

  return y
}

/**
 * 布局备注文本
 */
const layoutNotes = (noteDataArray, startX, startY) => {
  let y = startY
  noteDataArray?.map((it) => {
    y += VERTICAL_SPACE + DEFAULT_HEIGHT

    new TWEEN.Tween(it)
      .to(
        {x: startX, y, width: DEFAULT_WIDTH, height: DEFAULT_HEIGHT},
        ANIMATION_DURATION,
      )
      .easing(TWEEN.Easing.Quadratic.InOut)
      .start()
    // it.x = maxX
    // it.y = y += VERTICAL_SPACE + DEFAULT_HEIGHT
    // it.width = DEFAULT_WIDTH
    // it.height = DEFAULT_HEIGHT
  })

  return y
}
