import {CanvasEv, Id} from '../../../types'
import {FabricObject} from '../../shared/fabric'
import {isPointOnLineSegments} from '../../utils/fx'
import {isNumber} from '../../utils/verify'

export enum LineType {
  foreignKey = 'foreignKey',
}
export type NumLineTypes = 1 | 2 | 3 | 4

interface point {
  x: number
  y: number
}
export type Points = point[]

interface LineInput {
  id: Id
  pos: Points
  from: Id
  to: Id
  type?: LineType
  numLineTypes?: NumLineTypes
  lineWidth?: number
  remark?: string
  bdC?: string
}

export class LineErwin extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public id: Id
  public from: Id
  public to: Id
  public type: LineType
  public numLineTypes: NumLineTypes
  public pos: Points = []
  public remark: string
  public lineWidth = 1
  private bdC = '#000000'

  constructor(ctx: CanvasRenderingContext2D, input: LineInput) {
    super()
    this.ctx = ctx
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  private analysisOptions(input) {
    if (!input) return

    const {pos, type, from, to, id, lineWidth, numLineTypes, bdC} = input

    if (id) this.id = id
    if (from) this.from = from
    if (to) this.to = to
    if (pos) this.pos = pos
    if (type) this.type = type
    if (numLineTypes) this.numLineTypes = numLineTypes
    if (lineWidth) this.lineWidth = lineWidth
    if (bdC) this.bdC = bdC
  }

  public updateOptions(input: LineInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public draw(input?: LineInput) {
    this.analysisOptions(input)

    this.drawLine()
    this.drawEnds()
    this.drawNote()
  }

  public containsPoint(e: CanvasEv) {
    const {pos} = this

    const isOnline = isNumber(isPointOnLineSegments(e, pos, 5))

    return isOnline
  }

  public getIndexInPos(e) {
    return isPointOnLineSegments(e, this.pos, 5)
  }

  private drawLine() {
    const {ctx, bdC, lineWidth, type, pos} = this

    ctx.save()

    ctx.strokeStyle = bdC
    ctx.lineWidth = lineWidth

    // 根据是否是外键判断是否需要绘制虚线
    if (type === LineType.foreignKey) ctx.setLineDash([8, 5])

    pos.forEach((pointItem, pointIdx) => {
      if (pointIdx === 0) {
        ctx.beginPath()
        ctx.moveTo(pointItem.x, pointItem.y)
      } else {
        ctx.lineTo(pointItem.x, pointItem.y)
      }
    })

    ctx.stroke()
    ctx.restore()
  }

  private drawEnds() {
    const {ctx, pos, bdC, lineWidth, numLineTypes} = this
    const w = 15,
      posLen = pos.length || 0

    if (posLen < 2) return

    ctx.save()
    ctx.beginPath()
    ctx.setLineDash([0, 0])
    ctx.strokeStyle = bdC
    ctx.lineWidth = lineWidth

    //绘制起点标志
    //竖线
    if (pos[0].x === pos[1].x) {
      const dis = pos[1].y - pos[0].y
      const y =
        Math.abs(dis) < 10
          ? dis < 0
            ? pos[0].y - 1
            : pos[0].y + 1
          : dis < 0
          ? pos[0].y - 10
          : pos[0].y + 10
      ctx.moveTo(pos[0].x - w / 2, y)
      ctx.lineTo(pos[0].x + w / 2, y)
    }
    //横线
    else {
      const dis = pos[1].x - pos[0].x
      const x =
        Math.abs(dis) < 10
          ? dis < 0
            ? pos[0].x - 1
            : pos[0].x + 1
          : dis < 0
          ? pos[0].x - 10
          : pos[0].x + 10
      ctx.moveTo(x, pos[0].y - w / 2)
      ctx.lineTo(x, pos[0].y + w / 2)
    }

    //绘制终点标志
    //竖线
    if (pos[posLen - 2].x === pos[posLen - 1].x) {
      const dis = pos[posLen - 1].y - pos[posLen - 2].y
      const y =
        Math.abs(dis) < 10
          ? dis < 0
            ? pos[posLen - 1].y + 1
            : pos[posLen - 1].y - 1
          : dis < 0
          ? pos[posLen - 1].y + 10
          : pos[posLen - 1].y - 10
      ctx.moveTo(pos[posLen - 1].x - w / 2, y)
      ctx.lineTo(pos[posLen - 1].x + w / 2, y)
      ctx.stroke()
      //绘制分叉
      if ([1, 2].includes(numLineTypes)) {
        ctx.moveTo(pos[posLen - 1].x - w / 2, pos[posLen - 1].y)
        ctx.lineTo(pos[posLen - 1].x, y)
        ctx.lineTo(pos[posLen - 1].x + w / 2, pos[posLen - 1].y)
        ctx.stroke()
      }
      //绘制圆圈
      if ([1, 4].includes(numLineTypes)) {
        ctx.beginPath()
        const arcX = pos[posLen - 1].x
        const arcY = dis < 0 ? y + w / 2 : y - w / 2
        ctx.arc(arcX, arcY, (w - 2) / 2, 0, 360, false)
        ctx.fillStyle = '#fff'
        ctx.fill()
        ctx.stroke()
      }
    }
    //横线
    else {
      const dis = pos[posLen - 1].x - pos[posLen - 2].x
      const x =
        Math.abs(dis) < 10
          ? dis < 0
            ? pos[posLen - 1].x + 1
            : pos[posLen - 1].x - 1
          : dis < 0
          ? pos[posLen - 1].x + 10
          : pos[posLen - 1].x - 10
      ctx.moveTo(x, pos[posLen - 1].y - w / 2)
      ctx.lineTo(x, pos[posLen - 1].y + w / 2)
      ctx.stroke()
      //绘制分叉
      if ([1, 2].includes(numLineTypes)) {
        ctx.moveTo(pos[posLen - 1].x, pos[posLen - 1].y - w / 2)
        ctx.lineTo(x, pos[posLen - 1].y)
        ctx.lineTo(pos[posLen - 1].x, pos[posLen - 1].y + w / 2)
        ctx.stroke()
      }
      //绘制圆圈
      if ([1, 4].includes(numLineTypes)) {
        ctx.beginPath()
        const arcX = dis < 0 ? x + w / 2 : x - w / 2
        const arcY = pos[posLen - 1].y
        ctx.arc(arcX, arcY, (w - 2) / 2, 0, 360, false)
        ctx.fillStyle = '#fff'
        ctx.fill()
        ctx.stroke()
      }
    }

    ctx.restore()
  }

  private drawNote() {
    const {ctx, pos, remark} = this

    if (!remark) return

    const middleIndex = Math.floor(pos.length / 2) - 1

    let x, y, vertical
    // 中间线是横线
    if (pos[middleIndex].y === pos[middleIndex + 1].y) {
      vertical = false
      x =
        Math.abs(pos[middleIndex + 1].x - pos[middleIndex].x) / 2 +
        Math.min(pos[middleIndex + 1].x, pos[middleIndex].x)
      y = pos[middleIndex].y - 10
    }
    // 中间线是竖线
    else {
      vertical = true
      x = pos[middleIndex].x + 10
      y =
        Math.abs(pos[middleIndex + 1].y - pos[middleIndex].y) / 2 +
        Math.min(pos[middleIndex + 1].y, pos[middleIndex].y) -
        ctx.measureText(remark).width / 2
    }

    // 绘制文字
    ctx.textAlign = 'center'
    // ctx.textBaseline = 'center'
    vertical
      ? ctx.fillRowText(remark, x, y, 16, 16)
      : ctx.fillText(remark, x, y)
  }

  private calcBoundingBox() {
    const {pos} = this
    const xPoints = [],
      yPoints = []
    for (let i = 0; i < pos.length; i++) {
      const it = pos[i]
      xPoints.push(it.x)
      yPoints.push(it.y)
    }

    this.boundingBox = [
      [Math.min(...xPoints), Math.min(...yPoints)],
      [Math.max(...xPoints), Math.max(...yPoints)],
    ]
  }
}
