import {getTextWidth} from '../../../../core/utils/string'
import {
  PADDING,
  FONT_SIZE,
  FONT_FAMILY,
  LINE_HEIGHT,
  DEFAULT_HEIGHT,
  DEFAULT_WIDTH,
} from './conf'

export const getAutoSizeValue = (node) => {
  return {
    width: Math.max(
      Math.floor(
        Math.max(
          ...(node.attributes || []).map((it) =>
            getTextWidth(it.name, FONT_SIZE, FONT_FAMILY),
          ),
        ) +
          PADDING * 2 +
          FONT_SIZE * 2,
      ),
      DEFAULT_WIDTH,
    ),
    height: Math.floor(
      Math.max(
        ((node.attributes || []).length + 1) * LINE_HEIGHT + PADDING * 2,
        DEFAULT_HEIGHT,
      ),
    ),
  }
}
