<template>
  <div class="erg-export-data">
    <base-erg
      ref="erg"
      elName="canvas-export-data"
      :useThumbnail="true"
      :useRightMenu="true"
      :useAttrFocus="true"
      :editable="'edit'"
    />
  </div>
</template>

<script lang="ts" setup>
import {ref, onMounted, computed} from 'vue'
import BaseErg from './Base.vue'
import {ContextmenuEvent} from '@ej/erg-renderer/src'

const rightMenu = [
  [
    {
      icon: '',
      value: 'export',
      label: '导出数据',
    },
  ],
]

const props = withDefaults(
  defineProps<{
    useThumbnail: boolean
    useRightMenu: boolean
    useAttrFocus: boolean
    editable: string
  }>(),
  {
    useThumbnail: true,
    useRightMenu: true,
    useAttrFocus: true,
    editable: 'edit',
  },
)

const erg = ref()
const associationVisible = ref(false)

const graph = computed(() => erg.value.graph)
const useRightMenu = () => {
  graph.value.updateConfig({
    useRightMenu: true,
    rightMenu,
  })

  graph.value.on(ContextmenuEvent.Click, (info) => {
    switch (info.value) {
      case 'export':
        graph.value.canvas.exportJsonFile(graph.value.dataHandler.originData)
        break

      default:
        break
    }
  })
}

onMounted(useRightMenu)
</script>

<style lang="postcss">
.erg-export-data {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>
