import {NodeErwin} from '../../core/materials/node'
import {getAdjacentSides} from '../../core/utils/fx'
import {Erg} from '../index'

const TOLERANCE = 5
const COLOR = '#00BFFF'
export const useGuides = function (this: Erg) {
  const {
    renderElements,
    canvas: {eventCtx: ctx},
    eventObj: {target},
  } = this

  const findAdjacentElements = () => {
    const target = this.eventObj.target
    const adjacentElements = []
    if (!(target instanceof NodeErwin)) return adjacentElements

    for (let i = 0; i < renderElements.length; i++) {
      const it = renderElements[i]
      if (!(it instanceof NodeErwin)) continue

      const adjacentSides = getAdjacentSides(target, it, TOLERANCE)
      for (let j = 0; j < adjacentSides.length; j++) {
        const side = adjacentSides[j]
        adjacentElements.push({
          target: it,
          adjacentSides: side,
        })
      }
    }
    return adjacentElements
  }

  const draw = () => {
    const adjacentElements = findAdjacentElements()

    for (let i = 0; i < adjacentElements.length; i++) {
      const {target: it, adjacentSides} = adjacentElements[i]
      const side = adjacentSides[1]

      const startX =
        side === 'left' ? it.x : side === 'right' ? it.x + it.width : target.x
      const startY =
        side === 'top' ? it.y : side === 'bottom' ? it.y + it.height : target.y
      const endX =
        side === 'left' ? it.x : side === 'right' ? it.x + it.width : it.x
      const endY =
        side === 'top' ? it.y : side === 'bottom' ? it.y + it.height : it.y

      ctx.save()
      ctx.beginPath()
      ctx.setLineDash([4, 4])
      ctx.strokeStyle = COLOR
      ctx.moveTo(startX, startY)
      ctx.lineTo(endX, endY)
      ctx.stroke()
      ctx.restore()
    }
  }

  return {
    draw,
  }
}
