import {FabricObject} from '../../shared/fabric'
import {Id, CanvasEv} from '../../../types'
import {isNumber} from '../../utils/verify'

interface TextBaseInput {
  id?: Id
  content?: string
  x?: number
  y?: number
  bdC?: string
  textColor?: string
  bgC?: string
  borderWidth?: number
}

export class TextBase extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public x = 0
  public y = 0
  public width = 0
  public height = 0
  public bdC = '#0C64EB'
  public textColor = '#0C64EB'
  public bgC = '#D0E2FF'
  public borderWidth = 1

  public id: Id
  public content: string

  constructor(ctx: CanvasRenderingContext2D, input: TextBaseInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: TextBaseInput) {
    if (!input) return

    const {
      x,
      y,
      id,
      // width,
      // height,
      bdC,
      textColor,
      bgC,
      borderWidth,
      content,
    } = input

    if (isNumber(x)) this.x = x
    if (isNumber(y)) this.y = y
    // if (isNumber(width)) this.width = width
    // if (isNumber(height)) this.height = height
    if (id) this.id = id
    if (bdC) this.bdC = bdC
    if (textColor) this.textColor = textColor
    if (bgC) this.bgC = bgC
    if (isNumber(borderWidth)) this.borderWidth = borderWidth
    if (content) this.content = content
  }

  public updateOptions(input: TextBaseInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint(e: CanvasEv) {
    const {x: ex, y: ey} = e
    const {x, y, width, height} = this

    return ex >= x && ex <= x + width && ey >= y && ey <= y + height
  }

  public draw(input?: TextBaseInput) {
    this.updateOptions(input)

    const {ctx, content, x, y, textColor} = this
    ctx.save()
    ctx.textBaseline = 'top'
    ctx.fillStyle = textColor
    ctx.fillText(content, x, y)
    ctx.restore()
  }

  private calcBoundingBox() {
    const {x, y, ctx, content} = this
    const textMetrics = ctx.measureText(content)
    const width = (this.width = textMetrics.width)
    const height = (this.height =
      textMetrics.actualBoundingBoxAscent +
      textMetrics.actualBoundingBoxDescent)
    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }
}
