const prefix = 'L'
function S4(): string {
  return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
}

export function uuid(): string {
  return `${prefix}${S4()}${S4()}-${S4()}-${S4()}-${S4()}-${S4()}${S4()}${S4()}`
}

export function uuname(prefix: string, force = false): string {
  if (force) return prefix
  return `${prefix}-${S4()}${S4()}`
}
