interface Point {
  x: number
  y: number
}

interface Square extends Point {
  width: number
  height: number
}

/**
 * 检测点是否在线段上
 * @param point 检测点
 * @param segments 端点集合
 * @param tolerance 容差
 * @returns
 */
export const isPointOnLineSegments = (
  point: Point,
  segments: Point[],
  tolerance = 0,
): boolean | number => {
  const {x, y} = point
  const xTolerance = tolerance || 0
  const yTolerance = tolerance || 0
  for (let i = 0; i < segments.length - 1; i++) {
    const p1 = segments[i]
    const p2 = segments[i + 1]
    const a = p1.y - p2.y
    const b = p2.x - p1.x
    const c = p1.x * p2.y - p2.x * p1.y
    const distance = Math.abs(a * x + b * y + c) / Math.sqrt(a * a + b * b)
    if (
      distance <= xTolerance &&
      x >= Math.min(p1.x, p2.x) - xTolerance &&
      x <= Math.max(p1.x, p2.x) + xTolerance &&
      y >= Math.min(p1.y, p2.y) - yTolerance &&
      y <= Math.max(p1.y, p2.y) + yTolerance
    ) {
      return i
    }
  }
  return false
}

/**
 * 找到矩形临近边
 * @param square1 矩形1
 * @param square2 矩形2
 * @param tolerance 容差
 * @returns 匹配到的临近边
 */
export const getAdjacentSides = (
  square1: Square,
  square2: Square,
  tolerance = 0,
): string[][] => {
  const result = []
  if (square1 === square2) return result

  const leftDistance = Math.abs(square1.x - (square2.x + square2.width))
  if (leftDistance <= tolerance) {
    result.push(['left', 'right'])
  }

  const rightDistance = Math.abs(square2.x - (square1.x + square1.width))
  if (rightDistance <= tolerance) {
    result.push(['right', 'left'])
  }

  const topDistance = Math.abs(square1.y - (square2.y + square2.height))
  if (topDistance <= tolerance) {
    result.push(['top', 'bottom'])
  }

  const bottomDistance = Math.abs(square2.y - (square1.y + square1.height))
  if (bottomDistance <= tolerance) {
    result.push(['bottom', 'top'])
  }

  // Check if any of the edges are collinear
  if (Math.abs(square1.x - square2.x) <= tolerance) {
    result.push(['left', 'left'])
  }
  if (Math.abs(square1.y - square2.y) <= tolerance) {
    result.push(['top', 'top'])
  }
  if (
    Math.abs(square1.x + square1.width - square2.x - square2.width) <= tolerance
  ) {
    result.push(['right', 'right'])
  }
  if (
    Math.abs(square1.y + square1.height - square2.y - square2.height) <=
    tolerance
  ) {
    result.push(['bottom', 'bottom'])
  }

  return result
}
