import type {Erg} from '..'
import {uuid, uuname} from '../../core/utils/uuid'
import {Id} from '../../types'
import {
  NodeEvent,
  LineEvent,
  MarkEvent,
  NoteEvent,
  GraphEvent,
} from '../../types/event'
import type {
  NodeData,
  LineData,
  MarkData,
  NoteData,
  ErgData,
} from '../types/graph'

interface DataHandlerOptions {
  context?: Erg
  data: ErgData
}

/**
 * 可插拔的数据更新系统
 *  - 调用此类的mountAutoUpdate -> 渲染器直接修改原始数据
 *  - 不调用此类mountAutoUpdate -> 渲染器不修改原始数据，如需正确渲染，需要用户外部手动挂载监听修改数据
 */
export class DataHandler {
  private context: Erg
  public originData = {
    nodeDataArray: [],
    linkDataArray: [],
    markDataArray: [],
    noteDataArray: [],
  }
  public originDataMap: Map<Id, NodeData | LineData | MarkData | NoteData> =
    new Map()

  constructor(options: DataHandlerOptions) {
    this.update(options)
    this.mountAutoUpdate()
  }

  public update(options: DataHandlerOptions) {
    this.analysisOptions(options)
  }

  private analysisOptions(options: DataHandlerOptions) {
    const {data, context} = options
    if (data) this.originData = data
    if (context) this.context = context

    this.initMap()
  }

  private initMap() {
    const {
      originData: {
        nodeDataArray = [],
        linkDataArray = [],
        markDataArray = [],
        noteDataArray = [],
      },
      originDataMap,
    } = this

    originDataMap.clear()
    const elements = [
      ...nodeDataArray,
      ...linkDataArray,
      ...markDataArray,
      ...noteDataArray,
    ]
    for (let i = 0; i < elements.length; i++) {
      const el = elements[i]
      originDataMap.set(el.id, el)
    }
  }

  private mountAutoUpdate() {
    this.mountGraphEvent()
    this.mountNodeEvent()
    this.mountLineEvent()
    this.mountMarkEvent()
    this.mountNoteEvent()
    // graph.on('line:click', (id) => console.log('line:click', id))
    // graph.on('node:click', (id) => console.log('node:click', id))
    // graph.on('node-attr:click', (id) => console.log('node-attr:click', id))
  }

  private mountGraphEvent() {
    const {context} = this
    context.on(GraphEvent.Paste, (materials: any[]) => {
      materials.map((it) => {
        context.emit(NodeEvent.New, it)
      })
    })
  }

  private mountNodeEvent() {
    const {originDataMap, context} = this
    context.on(NodeEvent.Move, (node) => context.emit(NodeEvent.Change, node))
    context.on(NodeEvent.Resize, (node) => context.emit(NodeEvent.Change, node))
    context.on(NodeEvent.Change, (node) => {
      const it = originDataMap.get(node.id) as NodeData
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
      // it.color = node.color
      it.bdC = node.bdC
      it.bgC = node.bgC
    })
    context.on(
      NodeEvent.Delete,
      function (node) {
        const idx = this.originData.nodeDataArray.findIndex(
          (it) => it.id === node.id,
        )
        if (idx === -1) return

        this.originData.nodeDataArray.splice(idx, 1)
        originDataMap.delete(node.id)
      }.bind(this),
    )
    context.on(NodeEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
        name: uuname('Entity'),
      })
      this.originData.nodeDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountLineEvent() {
    const {originDataMap, context} = this
    context.on(LineEvent.Move, (line) => {
      const it = originDataMap.get(line.id) as LineData
      it.pos = line.pos
    })
    context.on(
      LineEvent.Delete,
      function (line) {
        const idx = this.originData.linkDataArray.findIndex(
          (it) => it.id === line.id,
        )
        if (idx === -1) return

        this.originData.linkDataArray.splice(idx, 1)
        originDataMap.delete(line.id)
      }.bind(this),
    )

    context.on(LineEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
      })
      this.originData.linkDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountMarkEvent() {
    const {originDataMap, context} = this
    context.on(MarkEvent.Move, (node) => context.emit(MarkEvent.Change, node))
    context.on(MarkEvent.Resize, (node) => context.emit(MarkEvent.Change, node))
    context.on(MarkEvent.Change, (node) => {
      const it = originDataMap.get(node.id) as MarkData
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
      // it.color = node.color
      it.bdC = node.bdC
      it.bgC = node.bgC
    })
    context.on(
      MarkEvent.Delete,
      function (mark) {
        const idx = this.originData.markDataArray.findIndex(
          (it) => it.id === mark.id,
        )
        if (idx === -1) return

        this.originData.markDataArray.splice(idx, 1)
        originDataMap.delete(mark.id)
      }.bind(this),
    )
    context.on(MarkEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
      })
      this.originData.markDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountNoteEvent() {
    const {originDataMap, context} = this
    context.on(NoteEvent.Move, (note) => context.emit(NoteEvent.Change, note))
    context.on(NoteEvent.Change, (note) => {
      const it = originDataMap.get(note.id) as NoteData
      it.x = note.x
      it.y = note.y
      it.width = note.width
      it.height = note.height
      it.color = note.color
      it.bdC = note.bdC
      it.bgC = note.bgC
    })
    context.on(
      NoteEvent.Delete,
      function (note) {
        const idx = this.originData.noteDataArray.findIndex(
          (it) => it.id === note.id,
        )
        if (idx === -1) return

        this.originData.noteDataArray.splice(idx, 1)
        originDataMap.delete(note.id)
      }.bind(this),
    )
    context.on(NoteEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
        text: 'remark...',
      })
      this.originData.noteDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private unmountAutoUpdate() {
    const {context} = this
    context.off()
  }

  private freeVariable() {
    this.originData = null
    this.originDataMap = null
  }

  public destroy() {
    this.unmountAutoUpdate()
    this.freeVariable()
  }
}
