export interface TreeNode {
  id: number | string
  children?: TreeNode[]
}

export function generateTree(
  nodeParents: Map<string | number, Set<string | number>>,
  rootId: number,
): TreeNode {
  const rootNode: TreeNode = {id: rootId}
  const queue: TreeNode[] = [rootNode]

  while (queue.length > 0) {
    const currentNode = queue.shift()!
    const children: TreeNode[] = []
    for (const [itId, itParents] of nodeParents) {
      const ifWaiting = nodeParents.has(itId)
      const ifChildren = itParents.has(currentNode.id)
      if (ifWaiting && ifChildren) {
        const node = {id: itId}
        children.push(node)
        queue.push(node)
        nodeParents.delete(itId)
      }
    }

    currentNode.children = children
  }
  return rootNode
}
