import {FabricObject} from '../../shared/fabric'

/**
 * 待优化：
 * 1.属性超出画布无需渲染
 */
interface NodeInput {
  x?: number
  y?: number
  width?: number
  height?: number
  bdC?: string
  targetWidth?: number
  targetBgColor?: string
  targetBdColor?: string
  lineWidth?: number
}
export class FrameResize extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public width = 100
  public height = 50
  public x = 0
  public y = 0
  private lineWidth = 1
  private bdC = 'blue'

  private targetWidth = 6
  private targetBgColor = '#FFFFFF'
  private targetBdColor = '#B4B4B4'

  constructor(ctx: CanvasRenderingContext2D, input: NodeInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: NodeInput) {
    if (!input) return

    const {
      x = 0,
      y = 0,
      width,
      height,
      targetBgColor,
      targetBdColor,
      bdC,
      lineWidth,
    } = input

    if (x) this.x = x
    if (y) this.y = y
    if (width) this.width = width
    if (height) this.height = height
    if (targetBgColor) this.targetBgColor = targetBgColor
    if (targetBdColor) this.targetBdColor = targetBdColor
    if (bdC) this.bdC = bdC
    if (lineWidth) this.lineWidth = lineWidth
  }

  public updateOptions(input: NodeInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint() {
    return false
  }

  public draw(input?: NodeInput) {
    this.updateOptions(input)

    this.drawBorder()
    this.drawTargets()
  }

  private drawBorder() {
    const {ctx, bdC, lineWidth, x, y, width, height} = this

    ctx.save()
    ctx.lineWidth = lineWidth
    ctx.strokeStyle = bdC
    ctx.strokeRect(x, y, width, height)
    ctx.restore()
  }

  private drawTargets() {
    const {
      ctx,
      targetBdColor,
      targetBgColor,
      targetWidth,
      x,
      y,
      width,
      height,
    } = this

    const halfTargetWidth = targetWidth / 2
    const fixX = x - halfTargetWidth
    const fixY = y - halfTargetWidth

    ctx.save()
    ctx.strokeStyle = targetBdColor
    ctx.fillStyle = targetBgColor

    ctx.fillRect(fixX, fixY, targetWidth, targetWidth)
    ctx.fillRect(fixX, fixY + height / 2, targetWidth, targetWidth)
    ctx.fillRect(fixX, fixY + height, targetWidth, targetWidth)
    ctx.fillRect(fixX + width / 2, fixY, targetWidth, targetWidth)
    ctx.fillRect(fixX + width / 2, fixY + height, targetWidth, targetWidth)
    ctx.fillRect(fixX + width, fixY, targetWidth, targetWidth)
    ctx.fillRect(fixX + width, fixY + height / 2, targetWidth, targetWidth)
    ctx.fillRect(fixX + width, fixY + height, targetWidth, targetWidth)

    ctx.strokeRect(fixX, fixY, targetWidth, targetWidth)
    ctx.strokeRect(fixX, fixY + height / 2, targetWidth, targetWidth)
    ctx.strokeRect(fixX, fixY + height, targetWidth, targetWidth)
    ctx.strokeRect(fixX + width / 2, fixY, targetWidth, targetWidth)
    ctx.strokeRect(fixX + width / 2, fixY + height, targetWidth, targetWidth)
    ctx.strokeRect(fixX + width, fixY, targetWidth, targetWidth)
    ctx.strokeRect(fixX + width, fixY + height / 2, targetWidth, targetWidth)
    ctx.strokeRect(fixX + width, fixY + height, targetWidth, targetWidth)

    ctx.restore()
  }

  private calcBoundingBox() {
    const {x, y, width, height} = this

    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }
}
