import {FabricObject} from '../../shared/fabric'

interface Options {
  x: number
  y: number
  w?: number
  h?: number
  fillStyle?: string
  strokeStyle?: string
  tiggerFunc?: () => void
}

export class LockIcon extends FabricObject {
  private ctx: CanvasRenderingContext2D
  private x = 0
  private y = 0
  private w = 20
  private h = 14
  private fillStyle = '#eeb174'
  private strokeStyle = '#333'
  private tiggerFunc: () => void

  constructor(ctx: CanvasRenderingContext2D, options: Options) {
    super()
    this.ctx = ctx
    this.analysisOptions(options)
  }

  private analysisOptions(options) {
    if (options.x) this.x = options.x
    if (options.y) this.y = options.y
    if (options.w) this.w = options.w
    if (options.h) this.h = options.h
    if (options.fillStyle) this.fillStyle = options.fillStyle
    if (options.strokeStyle) this.strokeStyle = options.strokeStyle
    if (options.tiggerFunc) this.tiggerFunc = options.tiggerFunc
  }

  public draw() {
    const {ctx, x, y, w, h, fillStyle, strokeStyle} = this
    ctx.save()
    ctx.fillStyle = fillStyle
    ctx.strokeStyle = strokeStyle
    ctx.lineWidth = 3
    ctx.beginPath()
    ctx.arc(x + w / 2, y - 3, w / 3, 0, Math.PI, true)
    ctx.stroke()
    ctx.drawRoundedRect(x, y, w, h, 2, 2, 2, 2, true, true, false, false)
    ctx.restore()
  }

  public containsPoint(e: MouseEvent, trigger = false) {
    const {x, y, w, h} = this
    const {x: ex, y: ey} = e
    const lines = this.getImageLines({
      tl: {x: x, y: y},
      tr: {x: x + w, y: y},
      bl: {x: x, y: y + h},
      br: {x: x + w, y: y + h},
    })

    const xpoints = this.findCrossPoints(ex, ey, lines)
    const isContains = Boolean(xpoints % 2)

    if (isContains && trigger) this.tiggerFunc?.()
    return isContains
  }
}
