<template>
  <div class="erg-right-menu">
    <base-erg
      ref="erg"
      elName="canvas-right-menu"
      :useThumbnail="true"
      :useRightMenu="true"
      :useAttrFocus="true"
      :editable="'edit'"
    />
    <parent-association
      v-if="associationVisible"
      id="11"
      elName="canvas-parent-association"
      :useThumbnail="true"
      :useRightMenu="true"
      :useAttrFocus="true"
      :editable="'edit'"
      v-model:dialogVisible="associationVisible"
    ></parent-association>
  </div>
</template>

<script lang="ts" setup>
import {ref, onMounted, computed} from 'vue'
import BaseErg from './Base.vue'
import {rightMenu, initRightMenu} from './assets/js/right-menu'
import ParentAssociation from './components/parent-association.vue'
import {ContextmenuEvent} from '@ej/erg-renderer/src'

const props = withDefaults(
  defineProps<{
    useThumbnail: boolean
    useRightMenu: boolean
    useAttrFocus: boolean
    editable: string
  }>(),
  {
    useThumbnail: true,
    useRightMenu: true,
    useAttrFocus: true,
    editable: 'edit',
  },
)

const erg = ref()
const associationVisible = ref(false)

const graph = computed(() => erg.value.graph)
//
const useRightMenu = () => {
  console.log(graph.value, 'erg')
  graph.value.updateConfig({
    useRightMenu: true,
  })

  graph.value.on(ContextmenuEvent.Show, (info) => {
    console.log(initRightMenu)
    graph.value.updateConfig({
      rightMenu: info.target ? rightMenu : initRightMenu,
    })
  })
  graph.value.on(ContextmenuEvent.Click, (info) => {
    console.log(info.target)
    switch (info.value) {
      case 'level-1':
        associationVisible.value = true
        break
      case 'level-2':
        associationVisible.value = true
        break
      case 'level-all':
        associationVisible.value = true
        break

      default:
        break
    }
  })
}

onMounted(useRightMenu)
</script>

<style lang="postcss">
.erg-right-menu {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>
