<template>
  <el-dialog
    title="Preview"
    v-model="dialogVisibles"
    :append-to-body="true"
    :close-on-click-modal="false"
    :before-close="handleClose"
    width="80%"
    class="dialog-association"
  >
    <div class="association__content">
      <div :id="props.elName" class="association"></div>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import {
  reactive,
  onBeforeUnmount,
  onMounted,
  watch,
  onUnmounted,
  nextTick,
  computed,
} from 'vue'
import {Erg as Graph} from '@ej/erg-renderer/src'
import {ElDialog} from 'element-plus'

const props = defineProps({
  dialogVisible: {
    type: Boolean,
    required: true,
  },
  useThumbnail: {
    type: Boolean,
    required: true,
    default: true,
  },
  useRightMenu: {
    type: Boolean,
    required: true,
    default: true,
  },
  useAttrFocus: {
    type: Boolean,
    required: true,
    default: true,
  },
  editable: {
    type: String,
    required: true,
    default: 'edit',
  },
  elName: {
    type: String,
    required: true,
    default: 'canvas',
  },
})

const emit = defineEmits(['update:dialogVisible'])
const dialogVisibles = computed((_) => props.dialogVisible)
const handleClose = () => emit('update:dialogVisible', false)

let graph

const mountGraph = () => {
  graph = new Graph({
    el: document.getElementById(props.elName),
    useThumbnail: true,
  })
}
const unmountGraph = () => graph.destroy()

onMounted(() => nextTick(mountGraph))
onBeforeUnmount(unmountGraph)
</script>

