<script lang="ts" setup>
import {reactive, onBeforeUnmount, onMounted, ref} from 'vue'
import {
  ElButton,
  ElDivider,
  ElColorPicker,
  ElSelect,
  ElOption,
} from 'element-plus'
import {
  Erg as Graph,
  ErgViewType,
  GraphEvent,
  NodeEvent,
  LineEvent,
  MarkEvent,
  NoteEvent,
  ContextmenuEvent,
  Editable,
  ColorCategory,
} from '@ej/erg-renderer/src'
import {defaultData as data} from './assets/js/mock'
// import { defaultData as data } from './assets/js/mock-gpt'
import Icons from './assets/icon/symbol-defs.svg'

const props = withDefaults(
  defineProps<{
    useThumbnail: boolean
    useRightMenu: boolean
    useAttrFocus: boolean
    editable: string
    elName: string
  }>(),
  {
    useThumbnail: true,
    useRightMenu: true,
    useAttrFocus: true,
    editable: Editable.Edit,
    elName: 'canvas',
  },
)

// 渲染器实例无需设为代理数据，这里只是方便Expose使用！
const graph = ref()

const state = reactive({
  scale: 100,
  bgC: '',
  bdC: '',
  color: '',
  editable: Editable.Edit,
  viewType: ErgViewType.Detail,
})

const mountGraph = () => {
  const fontFamily =
    '-apple-system, Noto Sans, Helvetica Neue, Helvetica, Nimbus Sans L, Arial, Liberation Sans, PingFang SC, Hiragino Sans GB, Noto Sans CJK SC, Source Han Sans SC, Source Han Sans CN, Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Zen Hei, ST Heiti, SimHei, WenQuanYi Zen Hei Sharp, sans-serif'
  graph.value = new Graph({
    el: document.getElementById(props.elName),
    fontFamily,
    editable: state.lock,
    useThumbnail: props.useThumbnail,
    useAttrFocus: props.useAttrFocus,
    useRightMenu: false,
  })
  console.log('data', data)

  graph.value.update(data)

  graph.value.on(GraphEvent.Copy, (info) => console.log(GraphEvent.Copy, info))
  graph.value.on(GraphEvent.Paste, (info) =>
    console.log(GraphEvent.Paste, info),
  )
  graph.value.on(GraphEvent.Keydown, (info) =>
    console.log(GraphEvent.Keydown, info),
  )
  // graph.value.on(GraphEvent.Zoom, (info) => console.log(GraphEvent.Zoom, info))
  // graph.value.on(GraphEvent.Move, (info) => console.log(GraphEvent.Move, info))
  // graph.value.on(GraphEvent.BeforeUpdate, (ctx) => null)
  // graph.value.on(GraphEvent.AfterUpdate, (ctx) => null)
  // graph.value.on(GraphEvent.Mousedown, (info) => console.log(GraphEvent.Mousedown, info))
  // graph.value.on(GraphEvent.Mouseup, (info) => console.log(GraphEvent.Mouseup, info))
  // graph.value.on(ContextmenuEvent.Show, (info) => null)
  // graph.value.on(ContextmenuEvent.Click, (info) => null)
  graph.value.on(NodeEvent.New, (info) => console.log(NodeEvent.New, info))
  graph.value.on(NodeEvent.Change, (info) =>
    console.log(NodeEvent.Change, info),
  )
  graph.value.on(NodeEvent.Delete, (info) =>
    console.log(NodeEvent.Delete, info),
  )
  // graph.value.on(NodeEvent.Move, (info) => console.log(NodeEvent.Move, info))
  // graph.value.on(NodeEvent.Enter, (info) => console.log(NodeEvent.Enter, info))
  // graph.value.on(NodeEvent.Leave, (info) => console.log(NodeEvent.Leave, info))
  // graph.value.on(NodeEvent.Mousedown, (info) => console.log(NodeEvent.Mousedown, info))
  // graph.value.on(NodeEvent.Mouseup, (info) => console.log(NodeEvent.Mouseup, info))
  graph.value.on(LineEvent.New, (info) => console.log(LineEvent.New, info))
  graph.value.on(LineEvent.Delete, (info) =>
    console.log(LineEvent.Delete, info),
  )
  // graph.value.on(LineEvent.Enter, (info) => console.log(LineEvent.Enter, info))
  // graph.value.on(LineEvent.Leave, (info) => console.log(LineEvent.Leave, info))
  // graph.value.on(LineEvent.Mousedown, (info) => console.log(LineEvent.Mousedown, info))
  // graph.value.on(LineEvent.Mouseup, (info) => console.log(LineEvent.Mouseup, info))
  graph.value.on(MarkEvent.New, (info) => console.log(MarkEvent.New, info))
  graph.value.on(MarkEvent.Delete, (info) =>
    console.log(MarkEvent.Delete, info),
  )
  graph.value.on(MarkEvent.Change, (info) =>
    console.log(MarkEvent.Change, info),
  )
  // graph.value.on(MarkEvent.Enter, (info) => console.log(MarkEvent.Enter, info))
  // graph.value.on(MarkEvent.Leave, (info) => console.log(MarkEvent.Leave, info))
  // graph.value.on(MarkEvent.Mousedown, (info) => console.log(MarkEvent.Mousedown, info))
  // graph.value.on(MarkEvent.Mouseup, (info) => console.log(MarkEvent.Mouseup, info))
  graph.value.on(NoteEvent.New, (info) => console.log(NoteEvent.New, info))
  graph.value.on(NoteEvent.Delete, (info) =>
    console.log(NoteEvent.Delete, info),
  )
  graph.value.on(NoteEvent.Change, (info) =>
    console.log(NoteEvent.Change, info),
  )
  // graph.value.on(NoteEvent.Enter, (info) => console.log(NoteEvent.Enter, info))
  // graph.value.on(NoteEvent.Leave, (info) => console.log(NoteEvent.Leave, info))
  // graph.value.on(NoteEvent.Mousedown, (info) => console.log(NoteEvent.Mousedown, info))
  // graph.value.on(NoteEvent.Mouseup, (info) => console.log(NoteEvent.Mouseup, info))
}

const changeCanvasScale = (scaleRatio) => {
  if (state.scale > 20) state.scale += scaleRatio
  graph.value.zoom(state.scale)
}
const updateGraphStatus = (status) => graph.value.updateConfig({status})
const setEditable = (editable) => {
  state.editable = editable
  graph.value.updateConfig({editable})
}
const changeColor = (value, type) => graph.value.setColor(value, type)
const updateLayout = () => graph.value.updateLayout()
const handleFullscreen = () => graph.value.fullscreen()
const unmountGraph = () => graph.value.destroy()

onMounted(mountGraph)
onBeforeUnmount(unmountGraph)

defineExpose({
  graph,
})
</script>

